// WORD GAMES
export const calculateWordGamesProgress = async (level) => {
  console.log('üö® D√âBUT calculateWordGamesProgress pour niveau:', level);
  try {
    // ‚úÖ CORRIG√â : Utiliser la m√™me cl√© que dans constants.js et useWordGames.js
    const storageKey = `word_games_completed_${level}`;
    console.log('üö® StorageKey Word Games:', storageKey);
    
    // üîç DEBUG : V√©rifier AsyncStorage
    const savedData = await AsyncStorage.getItem(storageKey);
    console.log('üîç DEBUG - AsyncStorage.getItem result:', savedData);
    
    if (!savedData) {
      console.log('üö® Aucune donn√©es sauvegard√©es pour Word Games niveau', level);
      return 0;
    }
    
    const data = JSON.parse(savedData);
    console.log('üîç DEBUG - Donn√©es pars√©es depuis AsyncStorage:', data);
    console.log('üîç DEBUG - Structure des donn√©es:', Object.keys(data));
    
    // ‚úÖ CORRIG√â : Les donn√©es sont directement l'objet completedGames
    const completedGames = data || {};  // ‚Üê Les donn√©es sont directement l'objet completedGames
    console.log('üîç DEBUG - completedGames extrait:', completedGames);
    
    // ‚úÖ CORRIG√â : R√©cup√©rer les VRAIES donn√©es comme les autres modules
    const wordGamesData = getWordGamesData(level);
    console.log('üîç DEBUG - getWordGamesData result:', wordGamesData);
    console.log('üîç DEBUG - Structure wordGamesData:', Object.keys(wordGamesData || {}));
    
    if (!wordGamesData) {
      return 0;
    }
    
    console.log('üîç DEBUG - Nombre de jeux:', wordGamesData.games.length);
    
    // ‚úÖ CORRIG√â : COPIER EXACTEMENT la logique de wordGamesStats.js
    // calculateTotalGames(wordGamesData)
    const totalGames = wordGamesData.games?.length || 0;
    console.log('üîç DEBUG - Total jeux calcul√©:', totalGames);
    
    // calculateCompletedGamesCount(completedGames) - LOGIQUE EXACTE
    const completedCount = Object.values(completedGames).filter(game => game.completed).length;
    console.log('üîç DEBUG - Total jeux compl√©t√©s:', completedCount);
    
    // calculateTotalProgress - LOGIQUE EXACTE
    const percentage = totalGames > 0 ? Math.min(100, (completedCount / totalGames) * 100) : 0;
    console.log('üîç DEBUG - Pourcentage final word games (calcul manuel):', percentage);
    
    return Math.round(percentage);
  } catch (error) {
    console.error('üö® Erreur dans calculateWordGamesProgress:', error);
    return 0;
  }
};

// ‚úÖ SUPPRIM√â : calculateAssessmentProgress n'est plus utilis√©

// =================== CALCUL GLOBAL - CORRIG√â ===================

export const calculateAllProgress = async () => {
  console.log('üö® D√âBUT calculateAllProgress');
  
  // üîç DEBUG : Lister toutes les cl√©s de stockage disponibles
  try {
    const allKeys = await AsyncStorage.getAllKeys();
    console.log('üîç TOUTES LES CL√âS DE STOCKAGE DISPONIBLES:', allKeys);
    
    // Filtrer les cl√©s qui pourraient contenir des donn√©es de progression
    const progressKeys = allKeys.filter(key => 
      key.includes('vocabulary') || 
      key.includes('grammar') || 
      key.includes('reading') || 
      key.includes('phrases') || 
      key.includes('conversation') || 
      key.includes('error') || 
      key.includes('word') || 
      key.includes('assessment')
    );
    console.log('üîç CL√âS DE PROGRESSION TROUV√âES:', progressKeys);
  } catch (error) {
    console.warn('üîç Erreur lors de la r√©cup√©ration des cl√©s:', error);
  }
  
  try {
    const newLevelProgress = {};
    const newExerciseProgress = {};

    // ‚úÖ MODIFI√â : Exclure le niveau bonus, s'arr√™ter au niveau 6
    const levels = ['1', '2', '3', '4', '5', '6'];
    
    const exerciseCalculators = {
      vocabulary: calculateVocabularyProgress,
      grammar: calculateGrammarProgress,
      reading: calculateReadingProgress,
      phrases: calculatePhrasesProgress,
      conversations: calculateConversationsProgress,
      wordGames: calculateWordGamesProgress,
    };

    await Promise.all(
      levels.map(async (level) => {
        console.log(`üö® Calcul progression pour niveau: ${level}`);
        
        // ‚úÖ SIMPLIFI√â : Plus de logique bonus, tous les exercices sont disponibles
        const availableExercises = Object.keys(exerciseCalculators);

        console.log(`üö® Exercices disponibles pour niveau ${level}:`, availableExercises);

        const results = await Promise.all(
          availableExercises.map(async (exerciseType) => {
            try {
              console.log(`üö® Calcul ${exerciseType} pour niveau ${level}`);
              const calculator = exerciseCalculators[exerciseType];
              const value = await calculator(level);
              console.log(`üö® R√©sultat ${exerciseType} niveau ${level}: ${value}%`);
              return { exerciseType, value };
            } catch (e) {
              console.warn(`Erreur calcul ${exerciseType} niveau ${level}:`, e);
              return { exerciseType, value: 0 };
            }
          })
        );

        let levelTotal = 0;
        let levelCompleted = 0;
        results.forEach(({ exerciseType, value }) => {
          if (!newExerciseProgress[exerciseType]) newExerciseProgress[exerciseType] = {};
          newExerciseProgress[exerciseType][level] = value;
          levelTotal += 1;  // ‚úÖ CORRIG√â : Compter le nombre d'exercices
          levelCompleted += value;
        });

        newLevelProgress[level] = levelTotal > 0
          ? Math.round(levelCompleted / levelTotal)  // ‚úÖ CORRIG√â : Moyenne simple
          : 0;
        
        console.log(`üö® Progression niveau ${level}: ${newLevelProgress[level]}%`);
      })
    );

    console.log('üö® FIN calculateAllProgress - newExerciseProgress:', newExerciseProgress);
    console.log('üö® FIN calculateAllProgress - newLevelProgress:', newLevelProgress);
    return { levelProgress: newLevelProgress, exerciseProgress: newExerciseProgress };
    
  } catch (error) {
    console.error('üö® Erreur calcul progression globale:', error);
    return { levelProgress: {}, exerciseProgress: {} };
  }
};

// =================== GETTERS UTILES ===================

export const getLevelProgress = (levelProgress, level) => {
  return levelProgress[level] || 0;
};

export const getExerciseProgress = (exerciseProgress, exerciseType, level) => {
  const progress = exerciseProgress[exerciseType]?.[level] || 0;
  
  // ‚úÖ AJOUT√â : Log de debug pour voir ce qui est retourn√©
  console.log(`üîç getExerciseProgress(${exerciseType}, ${level}):`, {
    exerciseProgress: exerciseProgress[exerciseType],
    levelProgress: exerciseProgress[exerciseType]?.[level],
    finalProgress: progress
  });
  
  return progress;
};

export const hasProgress = (exerciseProgress, exerciseType, level) => {
  return getExerciseProgress(exerciseProgress, exerciseType, level) > 0;
};

// =================== FONCTION D'AIDE POUR GESTION ERREUR ===================

const handleProgressError = (error, functionName, level, defaultValue) => {
  console.error(`üö® ERREUR dans ${functionName} pour niveau ${level}:`, error);
  console.error('üîç Stack trace:', error.stack);
  return defaultValue;
};